/**
 * 
 */
import { FormItemProps, Form, Select, Input, DatePicker, Space, InputNumber } from 'antd';
import * as C from '../../../config/academic.config';
import * as React from 'react';
import { disabledFormCurrentDate, requiredRule } from '@/config/form';
import FormListSkeleton from '../../form-list-skeleton';
import moment from 'moment';
import { RangePickerProps } from 'antd/lib/date-picker/generatePicker';
import CooperateForm, { CooperateFormValue } from '../../cooperate-form';
import { UploadFile } from 'antd/lib/upload/interface';
import UploadDragger from '@/components/upload-file';
import { FORMAT_DATE } from '@/config/time';
import FmtDatePicker from '@/components/fmt-date-picker';

interface IProps extends FormItemProps {

}

export interface ScientificFormValue extends CooperateFormValue {
  level: C.ScientificScoreItem['level'];
  name: string;
  time: RangePickerProps<moment.MomentInput>['value'];
  funds_actually_received: number;
  funds_due: number;
  distribute: number;
  files: UploadFile[];
}

export const scientificDefaultFormValue: ScientificFormValue = {
  level: 'bureau',
  name: '',
  time: [FORMAT_DATE, FORMAT_DATE],
  order: 1,
  partners: 1,
  distribute: 0,
  funds_due: 0,
  funds_actually_received: 0,
  files: []
};

/**
 * 
 */
const isPaymentProject = (level: C.ScientificScoreItem['level']) =>
  ["national", "provincial", "bureau"].includes(level);



const ScientificForm: React.FC<IProps> = (props) => {

  const scientificOptions = React.useMemo(() => C.scientificScoreList.map(item => ({
    label: `${item.title} ${item.score}`,
    value: item.level,
    score: item.score
  })), []);

  return (
    <FormListSkeleton
      title=""
      name="scientific"
      itemTitle=""
      listId="academic-form-scientific"
      alertMessage={
        [
          ' 1  SCI 2 ()',
          '',
          ''
        ]
      }
    >
      {
        (field) => (
          <React.Fragment>
            <Form.Item
              label=""
              name={[field.name, "level"]}
              rules={requiredRule}
            >
              <Select
                options={scientificOptions}
              />
            </Form.Item>
            <Form.Item
              label=""
              name={[field.name, "name"]}
              rules={requiredRule}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label=""
              name={[field.name, "time"]}
              rules={requiredRule}
            >
              <FmtDatePicker
                type="range"
                disabledDate={disabledFormCurrentDate}
              />
            </Form.Item>
            <Form.Item
              label=""
            >
              <Space wrap>
                <Form.Item
                  noStyle
                  name={[field.name, "funds_actually_received"]}
                  rules={requiredRule}
                >
                  <InputNumber
                    addonBefore=""
                    addonAfter=""
                    placeholder=""
                    min={0}
                  />
                </Form.Item>
                <Form.Item
                  noStyle
                  name={[field.name, "funds_due"]}
                  rules={requiredRule}
                >
                  <InputNumber
                    addonBefore=""
                    addonAfter=""
                    placeholder=""
                    min={0}
                  />
                </Form.Item>
                <Form.Item
                  name={[field.name, "distribute"]}
                  noStyle
                  rules={requiredRule}
                >
                  <InputNumber
                    addonBefore=""
                    addonAfter=""
                    min={0}
                  />
                </Form.Item>
              </Space>
            </Form.Item>
            <CooperateForm
              field={field}
            />
            <UploadDragger
              formProps={{
                label: "",
                name: [field.name, "files"]
              }}
            />
          </React.Fragment>
        )
      }
    </FormListSkeleton>
  )
};

export default ScientificForm;