import React, { useState, useEffect } from 'react';
import { Anchor } from 'antd';
import style from '../style/anchor.module.less';

const { Link } = Anchor;


const FormAnchor: React.FC = () => {
  const [targetOffset, setTargetOffset] = useState<number | undefined>(undefined);
  useEffect(() => {
    setTargetOffset(window.innerHeight / 2);
  }, []);
  return (
    <React.Fragment>
      <Anchor
        showInkInFixed
        targetOffset={targetOffset}
        className={style['left-anchor-nav']}
        // 98 pageHeader + top 10
        offsetTop={118}
      >
        <Link href="#moral-form" title="" />
        <Link href="#practice-form" title="">
          <Link href="#practice-form-result" title=""></Link>
          <Link href="#practice-form-social" title="">
            <Link href="#practice-form-social-cadre" title=""></Link>
            <Link href="#practice-form-social-activity" title=""></Link>
          </Link>
          <Link href="#practice-form-competition" title=""></Link>
        </Link>
        <Link href="#academic-form" title="">
          <Link href="#academic-form-scientific" title=""></Link>
          <Link href="#academic-form-award" title="" />
          <Link href="#academic-form-dissertation" title="" />
          <Link href="#academic-form-publish" title="" />
        </Link>
      </Anchor>
    </React.Fragment>
  );
};

export default FormAnchor;